nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: rgb(3, 3, 41);
    padding: 20px 80px;
    box-shadow:0 5px 15px rgba(0, 0, 0, 0.06);
}

#navbar{
    display: flex;
    align-items: center;
    justify-content: center;
}

#navbar li{
    list-style: none;
    padding: 0 20px;
    position: relative;
    color: aliceblue;
}

#navbar li a{
    text-decoration: none;
    font-size: 1.3rem;
    font-weight: 600;
    color: white;
    transition: 0.3s ease-in-out;
}
#navbar li a:hover,
#navbar li a.active{
    color: aquamarine;
}

#navbar li a:hover::after,
#navbar li a.active::after{
content: '';
width: 30%;
height: 2px;
background: aquamarine;
position: absolute;
bottom: -4px;
left: 20px;
}
#mobile{
    color: white;
    align-items: center;
}
